* {
  box-sizing: border-box;
}

body, #root {
  background-color: #34495e;
  background-image: url('./assets/abbey-road-bg.png');
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  margin: 0;
  overflow: hidden;
}


.header {
  margin: 20px 0 0;
  padding-left: 5%;
  padding-right: 5%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
h1{
  max-width: 80%;
  text-align: center;
}

.game-container {
  padding: 20px 30px;
  position: relative;
  margin: auto;
  height: 650px;
  width: 500px;
  max-width: 100%;
}

.figure-container {
  fill: transparent;
  stroke: #fff;
  stroke-width: 4px;
  stroke-linecap: round;
}

.figure-part {
  display: none;
}

.wrong-letters-container {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  text-align: right;
}

.wrong-letters-container p {
  margin: 0 0 5px;
}

.wrong-letters-container span {
  font-size: 24px;
}
.words-container{
  display: flex;
  /* display: inline-block; */
  flex-direction: row;
  justify-content: space-around;
  /* justify-items: stretch; */
  width: 100%;
  flex-wrap: wrap;
  bottom: 10px;
  left: 50%;
  /* height: 200px; */
  /* transform: translateX(-50%); */
  padding-left: 30%;
  

}
.word {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-left: 10px;
  /* display: flex; */
  /* position: absolute; */
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 20px;
  padding-right: 20px;
}


.letter {
  /* border-bottom: 3px solid #2980b9; */
  border-bottom: 3px solid #ededed;
  display: inline-flex;
  font-size: 30px;
  align-items: center;
  justify-content: center;
  margin: 0 3px;
  height: 50px;
  width: 20px;
  /* padding-left: 2px;
  padding-right: 2px; */
}

.popup-container {
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* display: flex; */
  display: none;
  align-items: center;
  justify-content: center;
}

.popup {
  background: #2980b9;
  border-radius: 5px;
  box-shadow: 0 15px 10px 3px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
}

.popup button {
  cursor: pointer;
  background-color: #fff;
  color: #2980b9;
  border: 0;
  margin-top: 20px;
  padding: 12px 20px;
  font-size: 16px;
}

.popup button:active {
  transform: scale(0.98);
}

.popup button:focus {
  outline: 0;
}

.notification-container {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 10px 10px 0 0;
  padding: 15px 20px;
  position: absolute;
  bottom: -50px;
  transition: transform 0.3s ease-in-out;
}

.notification-container p {
  margin: 0;
}

.notification-container.show {
  transform: translateY(-50px);
}
.credits{
  color: black;
  background-color: #ededed;
  width: 100%;
  text-align: center;
  font-style: italic;
  font-size: 15px;
}
@media only screen and (max-width: 650px) {
  .header{
    margin-top: 0;
  }
  .game-container {
    padding: 20px 30px;
    position: relative;
    margin: auto;
    height: 90vh;
    width: 100%;
    
  }
  .words-container{}
}